.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
select{
  padding: 5px 7px;
  margin:0 20px 0 5px;
}
select:active{
  outline: none;
}
input{
  padding: 5px 7px;
  margin:0 20px 0 5px;
}

input:active{
  outline: none;
}
button{
  padding: 8px 12px;
  letter-spacing: 2px;
  background-color: black;
  color: white;
  outline: none;
  border: none;
  border-radius: 3px;
}